<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class="data_sift">
                    <div class="data_title">
                        <span>数据筛选</span>
                    </div>
                    <el-row type="flex" justify="space-around">
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>产品名称：</el-col>
                                    <el-col :span='17'>
                                        <el-input v-model='name' @input='select'></el-input>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>产品类型：</el-col>
                                    <el-col :span='17'>
                                        <el-select v-model="product_t" placeholder="请选择" style='width:100%;'>
                                            <el-option v-for="(item,index) in options" :key="index" :label="item.typeName" :value="item.id">
                                            </el-option>
                                        </el-select>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='6'>
                            <div class="data_s">
                                <el-row>
                                    <el-col :span='7'>发布时间：</el-col>
                                    <el-col :span='17'>
                                        <el-date-picker style='width:100%;font-size:12px;' v-model="time" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
                                        </el-date-picker>
                                    </el-col>
                                </el-row>
                            </div>
                        </el-col>
                        <el-col :span='5'>
                            <div class="data_s">
                                <el-button type="primary" @click='select' icon="el-icon-search">查询</el-button>
                            </div>
                        </el-col>
                    </el-row>
                </div>
                <div class="data_list">
                    <div class="data_l_t">
                        <span>
                            数据列表
                        </span>
                        <el-button type="primary" @click='add' icon='el-icon-plus'>添加</el-button>
                    </div>
                    <div class="state">
                        <span @click='on' :class='num==0?"active":""'>已发布({{list.length}})</span>
                        <span @click='off' :class='num==1?"active":""'>未发布({{lists.length}})</span>
                    </div>
                    <!-- 已发布 -->
                    <div v-if='num==0'>
                        <el-table :data="list.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%;" :header-cell-style="rowClass" 
                        :cell-style="cellStyle" border>
                            <el-table-column prop="id" label="序号" width="100">
                            </el-table-column>
                            <el-table-column prop="id" label="产品编号">
                            </el-table-column>
                            <el-table-column prop="name" label="产品名称" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="typeName" label="产品类型">
                            </el-table-column>
                            <el-table-column label="图片">
                                <template slot-scope="scope">
                                    <img :src="scope.row.photo" alt="" style="width: 50px;height:50px">
                                </template>
                            </el-table-column>
                            <!-- <el-table-column
    					    prop="status"
    					    label="状态"
    					    :formatter='formats'>
    					  </el-table-column> -->
                            <el-table-column prop="creationTime" label="创建时间" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人">
                            </el-table-column>
                            <el-table-column label="操作">
                                <template scope='scope'>
                                    <span style='cursor: pointer;color:#0079FE;' @click.stop='re(scope.$index,scope.row)'><i class="el-icon-refresh"></i> 撤回</span>
                                </template>
                            </el-table-column>
                            <!--  <el-table-column
    					 label="操作">
    					 	<template scope='scope'>
    					                     			<span style='cursor: pointer;color:#0079FE;' @click.stop="edit(scope.$index,scope.row)"><i class="el-icon-edit"></i> 编辑</span>
    					                     	 	 </template>
    					     					</el-table-column> -->
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="list.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                    <!-- 未发布 -->
                    <div v-if='num==1'>
                        <el-table :data="lists.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%;" @row-click="handdles" :header-cell-style="rowClass" :cell-style="cellStyle" border>
                            <el-table-column prop="id" label="序号" width="100">
                            </el-table-column>
                            <el-table-column prop="id" label="产品编号">
                            </el-table-column>
                            <el-table-column prop="name" label="产品名称" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="typeName" label="产品类型">
                            </el-table-column>
                            <el-table-column label="图片">
                                <template slot-scope="scope">
                                    <img :src="scope.row.photo" alt="" style="width: 50px;height:50px">
                                </template>
                            </el-table-column>
                            <el-table-column prop="creationTime" label="创建时间" show-overflow-tooltip>
                            </el-table-column>
                            <el-table-column prop="realName" label="创建人">
                            </el-table-column>
                            <el-table-column label="操作">
                                <template scope='scope'>
                                    <span style='cursor: pointer;color:#0079FE;' @click.stop="del(scope.$index,scope.row.id)"><i class="el-icon-delete"></i> 删除</span>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-row>
                            <div class="pages">
                                <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pagesize" layout="prev, pager, next, jumper" :total="lists.length">
                                </el-pagination>
                            </div>
                        </el-row>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '产品管理',
            currentPage: 1,
            pagesize: 5,
            total: 1,
            product_list: [],
            num: 0,
            name: '',
            product_t: '',
            time: '',
            options: [],
            restaurants: []
        }
    },
    methods: {
        formatDate(date) {
            if (typeof date == "string") {
                return this.formatDate2(date);
            }
            var y = date.getFullYear();
            var M = date.getMonth() + 1;
            M = M < 10 ? "0" + M : M;
            var d = date.getDate();
            d = d < 10 ? "0" + d : d;
            var h = date.getHours();
            h = h < 10 ? "0" + h : h;
            var m = date.getMinutes();
            m = m < 10 ? "0" + m : m;
            var s = date.getSeconds();
            s = s < 10 ? "0" + s : s;
            return y + "-" + M + "-" + d + " " + h + "-" + m + "-" + s;
        },
        select() {
            var a;
            var b;
            if (this.time == '') {
                a = '';
                b = '';
            } else {
                a = this.formatDate(this.time[0])
                b = this.formatDate(this.time[1])
            }
            this.$axios.post('/tomlive/productInfo/selectProductInfoByCondition', this.$qs.stringify({
                name: this.name,
                productTypeId: this.product_t,
                beginTime: a,
                endTime: b,
                status: ''
            })).then((res) => {
                console.log(res)
                this.product_list = res.data.info
            })
        },
        on() {
            this.num = 0
        },
        off() {
            this.num = 1
        },
        /*撤回*/
        re(index, val) {
            this.$confirm('是否撤回?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/productInfo/updateProductInfoStatus', this.$qs.stringify({
                        id: val.id
                    })).then((res) => {
                        console.log(res)
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        },
        /*产品详情*/
        handdle(row, event, column) {
            console.log(row, event, column)
            window.sessionStorage.setItem('product_details', JSON.stringify(row))
            this.$router.push('/home/product_details');
        },
        /*未发布产品修改*/
        handdles(row, event, column) {
            console.log(row, event, column)
            window.sessionStorage.setItem('product_details', JSON.stringify(row))
            this.$router.push('/home/product_edit')
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        /*添加产品*/
        add() {
            this.$router.push('/home/add_product')
        },
        handleSizeChange(val) {
            // console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
        },
        formats(row, column, value) {
            if (value === 1) {
                return '发布'
            } else {
                return '未发布'
            }
        },
        /*删除产品*/
        del(index, id) {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/productInfo/deleteProductInfoByPrimaryKey', this.$qs.stringify({
                        id: id
                    })).then((res) => {
                        this.all()
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
            console.log(index, id)
        },
        edit(index, id) {
            console.log(index, id)
        },
        /*查询所有产品*/
        all() {
            this.$axios.post('/tomlive/productInfo/selectAllProductInfo').then((res) => {
                console.log(res)
                this.product_list = res.data.info.reverse()
                this.total = this.product_list.length
            })
        },
        /*模糊搜索*/
        handleSelect(item) {
            console.log(item);
        },
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        }
    },
    mounted() {
        this.all()
        this.$axios.post('/tomlive/productType/selectAllProductType').then((res) => {
            /*var arr=res.data.info;
            var arr2=[];
            for(var i=0;i<arr.length;i++){
            	arr2.push(arr[i].typeName)
            }*/
            // this.options=[...new Set(arr2)]
            this.options = res.data.info
        })
    },
    computed: {
        list() {
            var _this = this
            return this.product_list.filter(function(number) {
                return number.status == 1
            })
        },
        lists() {
            var _this = this
            return this.product_list.filter(function(number) {
                return number.status == 0
            })
        }
    }
}
</script>
<style scoped>
.active {
    color: #0079FE;
    border-bottom: 1px solid #0079FE;
}

.data_sift {
    width: 100%;
    height: 140px;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
    background: #fff;
    margin: 20px 0 0 0;
}

.data_title {
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.data_title>span {
    font-size: 14px;
    line-height: 50px;
    color: #666;
    font-weight: bold;
    margin-left: 20px;
}

.data_list {
    width: 100%;
    min-height: 500px;
    background: #fff;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    padding: 0 20px;
    margin: 20px 0 0 0;
}

.data_l_t {
    width: 100%;
    height: 60px;
    line-height: 60px;
    font-size: 14px;
    font-weight: bold;
    color: #666;
}

.data_l_t>button {
    margin: 10px 0px;
    float: right;
}

.data_s {
    width: 100%;
    line-height: 35px;
    height: 35px;
    margin: 20px 0 0 0;
    font-size: 14px;
}

.pages {
    margin: 20px 0 20px 0;
    float: right;
}

.state {
    width: 100%;
    height: 45px;
    margin: 0 0 20px 0;
}

.state>span {
    display: block;
    float: left;
    width: 100px;
    height: 45px;
    text-align: center;
    line-height: 45px;
    font-size: 14px;
    cursor: pointer;
}
</style>